.navbar {
    position: absolute;
    width: 100%;
    height: 20px;
    text-align: right;
}

.navbar-modified {
    display: grid;
    position: fixed;
    z-index: 100;
    width: 100%;
    height: auto;
    text-align: right;
    background-color: rgba(62,62,62,0.7);
    animation: slide-down 0.3s;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr;
    grid-template-areas: 
    "logo menu";
}

.navbar ul, .navbar-modified ul {
    grid-area: menu;
    justify-self: flex-end;
    display: inline-flex;
    height: 20px;
}

.navbar li, .navbar-modified li {
    padding: 5px;
    margin-left: 5px;
    list-style-type: none;
    color: white;
    height: 10px;
    cursor: pointer;
}

.navbar li a, .navbar-modified li a {
    text-decoration: none;
    color: white;
}

.navbar li a:hover, .navbar-modified li a:hover {
    color: rgb(157, 202, 157);
}

.navbar-modified img {
    grid-area: logo;
    padding: 5px;
    margin-left: 5px;
    width: 8%;
    height: auto;
}

@keyframes slide-down {
    0% {
        opacity: 0;
        transform: translateY(-100%);
    } 
    100% {
        opacity: 1;
        transform: translateY(0);
    } 
}

@media (max-width: 767px) {
    .navbar {
        display: block;
        position: absolute;
        width: 100%;
        text-align: right;
    }

    .navbar-modified {
        display: grid;
        position: fixed;
        z-index: 100;
        width: 100%;
        height: auto;
        animation: slide-down 0.3s;
        text-align: right;
        background-color: rgba(62,62,62,0.7);
        grid-template-columns: 1fr 1fr;
        grid-template-rows: 1fr;
        grid-template-areas: 
        "logo menu";
    }

    .navbar ul, .navbar-modified ul {
        height: 5%;
        grid-area: menu;
        justify-self: flex-end;
    }

    .navbar-modified img {
        grid-area: logo;
        padding: 5px;
        margin-left: 5px;
        width: 20%;
        height: auto;
    }

    .nav-icon {
      margin-right: 20px;  
      width: 40px;
    }

    .nav-icon:after, 
    .nav-icon:before, 
    .nav-icon div {
      background-color: #fff;
      border-radius: 3px;
      content: '';
      display: block;
      height: 5px;
      margin: 7px 0;
    }
    @keyframes slide-down {
    0% {
        opacity: 0;
        transform: translateY(-100%);
    } 
    100% {
        opacity: 1;
        transform: translateY(0);
    } 
}
}