.flecha {
    position: fixed;
    top: 50%;
    right: 0;
    color: white;
}

.flecha img {
    width: 70%;
    height: auto;
    cursor: pointer;
}

.flecha img:hover {
    filter: drop-shadow(0 0 0 black);
}

@media (max-width: 767px) {
    .flecha {
        display: none;
    }
}